import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import * as io from 'socket.io-client';
import firebase from 'firebase';


@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
	users: Array<any>;
	user: any;
	socket:any;
	constructor(public navCtrl: NavController) {
			this.users = [];
	  		this.user = firebase.auth().currentUser;
			this.socket = io('http://enko.duckdns.org:3000');
			this.socket.emit('join_room', {user:  this.user.email});
			
			this.socket.on('users_to_me', (data) => {
				for (var i = 0; i < data.users.length; i++) {
					this.checkAndAdd(data.users[i].username);
					//this.users.push(data.users[i]);
				}
			  
			});

	}


	checkAndAdd(username) {
	  var id = this.users.length + 1;
	  var found = this.users.some(function (el) {
	    return el.username === username;
	  });
	  if (!found) { this.users.push({ id: id, username: username }); }
	}

}



